@import url(https://fonts.googleapis.com/css?family=Nixie+One);
@import url(https://fonts.googleapis.com/css?family=League+Script);
* {
    box-sizing: border-box;
}

body{
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #eff3f7;
    height: 100%;
}
.about__container{
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93 ,0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    padding: 20px;
    border-radius: 5px;
    background-color: white;
    margin: 0 auto;
    margin-top: 100px;
    width: 100%;
    max-width: 500px;
    font-weight: 300;
}

.about__container span:first-child{
    font-size: 20px;
}

.about__container span:last-child{
    display: block;
    margin-top: 10px;
}
  
  @keyframes backgroundAnimate {
    from {
      left: 0;
      top: 0;
    }
    to {
      left: -10000px;
      top: -2000px;
    }
  }
  
  #back {
    background: url(http://www.tranexnet.com/img/back.png) repeat 20% 20%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0.4;
    z-index: -1;
    -webkit-animation-name: backgroundAnimate;
    -webkit-animation-duration: 500s;
    -webkit-animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
    -moz-animation-name: backgroundAnimate;
    -moz-animation-duration: 5s;
    -moz-animation-timing-function: linear;
    -moz-animation-iteration-count: infinite;
    -o-animation-name: backgroundAnimate;
    -o-animation-duration: 500s;
    -o-animation-timing-function: linear;
    -o-animation-iteration-count: infinite;
    animation-name: backgroundAnimate;
    animation-duration: 500s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }
  
  #front {
    background: url(http://www.tranexnet.com/img/front.png) repeat 35% 35%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0.6;
    z-index: -1;
    -webkit-animation-name: backgroundAnimate;
    -webkit-animation-duration: 300s;
    -webkit-animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
    -moz-animation-name: backgroundAnimate;
    -moz-animation-duration: 300s;
    -moz-animation-timing-function: linear;
    -moz-animation-iteration-count: infinite;
    -o-animation-name: backgroundAnimate;
    -o-animation-duration: 300s;
    -o-animation-timing-function: linear;
    -o-animation-iteration-count: infinite;
    animation-name: backgroundAnimate;
    animation-duration: 300s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }
.movies .movie {
    width: 400px;
    height: 600px;
    background-color: white;
    margin-bottom: 10px;
    margin-left: 10px;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #181818;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
      0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  }

  .movie ul {
    list-style:none;
    margin:0;
    padding:0;
}

.movie li {
    /* margin: 0 0 0 0;
    padding: 0 0 0 0;
    border : 0; */
    display: inline;
    border: 1px solid #3a3a3a;
    padding: 8px;
    border-radius: 5px;
    float: left;
    margin-right: 2px;
    margin-top: 2px;
    font-size: 4px;
    color: black;
}

.movie p {
    margin: 1;
    padding: 0;
    border : 0;
    float: left;
}
    
  .movie img {
    margin:auto;
    width:250px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
      0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
  }
 
  .movie title {
        font-size:20px;
        text-decoration: none;
  }
  
  
  @media only screen and (max-width:1270px) {
    .movie img {
      width:98%;
    }
    .movies .movie{ 
      margin: 5vh 2vw;
      width:30vw;
      height: auto;
    }
  }
  
  @media only screen and (max-width:768px) {
    .movie img {
      width:99%;
    }
    .movies .movie{ 
      margin: 3vh 2vw;
      width: 70vw;
      height: auto;
    }
  }

















  
.container{
    height: 100%;
    display: flex;
    justify-content: center;
}

.loder{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}

.movies{
    display: grid;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
    grid-gap: 100px;
    padding: 50px;
    width: 80%;
    padding-top: 70px;
}

@media screen and (max-width: 1090px){
    .movies{
        grid-template-columns: 1fr;
        width: 100%;
    }
}




